import React, { useContext, useState } from "react";
import { useNavigate } from "react-router-dom";
import {
  FaCheckCircle,
  FaTasks,
  FaUsers,
  FaChartLine,
  FaEye,
  FaEyeSlash,
} from "react-icons/fa";

import Card from "../../shared/components/UIElements/Card";
import Button from "../../shared/components/UIElements/Button";
import Input from "../../shared/components/UIElements/Input";
import ImageUpload from "../../shared/components/UIElements/ImageUpload";
import ErrorModal from "../../shared/components/UIElements/ErrorModal";
import LoadingSpinner from "../../shared/components/UIElements/LoadingSpinner";
import {
  VALIDATOR_EMAIL,
  VALIDATOR_MINLENGTH,
  VALIDATOR_REQUIRE,
} from "../../shared/util/validators";
import { useForm } from "../../shared/Hooks/form-hook";
import { useHttpClient } from "../../shared/Hooks/http-hook";
import { AuthContext } from "../../shared/context/auth-context";
import "./Auth.css";

const Auth = () => {
  const auth = useContext(AuthContext);
  const [isLoginMode, setIsLoginMode] = useState(false);
  const [showPassword, setShowPassword] = useState(false);
  const { isLoading, error, sendRequest, errorHandler } = useHttpClient();

  const [formState, inputHandler, setFormData] = useForm(
    {
      email: {
        value: "",
        isValid: false,
      },
      password: {
        value: "",
        isValid: false,
      },
    },
    false
  );
  const navigate = useNavigate();

  const switchHandler = () => {
    if (!isLoginMode) {
      setFormData(
        {
          ...formState.inputs,
          name: undefined,
          image: undefined,
        },
        formState.inputs.email.isValid && formState.inputs.password.isValid
      );
    } else {
      setFormData(
        {
          ...formState.inputs,
          name: {
            value: "",
            isValid: false,
          },
          image: {
            value: null,
            isValid: false,
          },
        },
        false
      );
    }

    setIsLoginMode((prevMode) => !prevMode);
  };

  const togglePasswordVisibility = () => {
    setShowPassword(!showPassword);
  };

  const sumbitAuthHandler = async (event) => {
    event.preventDefault();

    if (isLoginMode) {
      try {
        const data = await sendRequest(
          process.env.REACT_APP_BACKEND_URL + "/users/login",
          "POST",
          JSON.stringify({
            email: formState.inputs.email.value,
            password: formState.inputs.password.value,
          }),
          {
            "Content-Type": "application/json",
          }
        );

        auth.login(data.userId, data.token);
        navigate("/");
      } catch (error) {}
    } else {
      try {
        const formData = new FormData();

        formData.append("email", formState.inputs.email.value);
        formData.append("name", formState.inputs.name.value);
        formData.append("password", formState.inputs.password.value);
        formData.append("image", formState.inputs.image.value);

        const data = await sendRequest(
          process.env.REACT_APP_BACKEND_URL + "/users/signup",
          "POST",
          formData
        );

        auth.login(data.userId, data.token);
        navigate("/");
      } catch (error) {}
    }
  };

  return (
    <div className="auth-container">
      <ErrorModal error={error} onClear={errorHandler} />
      <div className="auth-content">
        <div className="auth-notes">
          <h2>{isLoginMode ? "Welcome Back!" : "Join Our Community"}</h2>
          <h3>
            {isLoginMode ? "Login to Your Account" : "Create Your Account"}
          </h3>
          <p>
            {isLoginMode
              ? "Access your tasks, track your progress, and stay organized with our powerful task management tools."
              : "Start your journey to better productivity. Create an account to access all features and begin organizing your tasks effectively."}
          </p>

          <div className="auth-features">
            <h4>Key Features:</h4>
            <ul>
              <li>
                <FaTasks /> Smart Task Management
              </li>
              <li>
                <FaUsers /> Team Collaboration
              </li>
              <li>
                <FaChartLine /> Progress Tracking
              </li>
              <li>
                <FaCheckCircle /> Achievement System
              </li>
            </ul>
          </div>
        </div>

        <Card className="auth">
          {isLoading && <LoadingSpinner />}
          <h2 className="auth-header">
            {isLoginMode ? "Sign In" : "Create Account"}
          </h2>

          <form onSubmit={sumbitAuthHandler}>
            {!isLoginMode && (
              <>
                <Input
                  id="name"
                  element="input"
                  label="Name"
                  type="text"
                  errorText="Please enter a name."
                  validators={[VALIDATOR_REQUIRE()]}
                  onInput={inputHandler}
                  placeholder="Enter your full name"
                />

                <ImageUpload
                  center
                  id="image"
                  onInput={inputHandler}
                  errorText="Please provide an image"
                />
              </>
            )}
            <Input
              id="email"
              element="input"
              label="E-mail"
              type="email"
              errorText="Please enter a valid email address."
              validators={[VALIDATOR_EMAIL()]}
              onInput={inputHandler}
              placeholder="Enter your email address"
            />
            <div className="password-input-container">
              <Input
                id="password"
                element="input"
                label="Password"
                type={showPassword ? "text" : "password"}
                errorText="Please enter a valid password, at least 6 characters."
                validators={[VALIDATOR_MINLENGTH(6)]}
                onInput={inputHandler}
                placeholder="Enter your password"
              />
              <button
                type="button"
                className="password-toggle-auth"
                onClick={togglePasswordVisibility}
              >
                {showPassword ? <FaEyeSlash /> : <FaEye />}
              </button>
            </div>

            <Button
              type="submit"
              className="auth__button"
              disabled={!formState.isValid}
            >
              {isLoginMode ? "Sign In" : "Create Account"}
            </Button>
          </form>

          <Button className="switch__button" onClick={switchHandler}>
            {isLoginMode
              ? "Need an account? Sign Up"
              : "Already have an account? Sign In"}
          </Button>
        </Card>
      </div>
    </div>
  );
};

export default Auth;
