import React, { useReducer, useEffect } from "react";

import { validate, VALIDATOR_REQUIRE } from "../../util/validators";
import "./Input.css";

const inputReducer = (state, action) => {
  switch (action.type) {
    case "CHANGE":
      return {
        ...state,
        value: action.val,
        isValid: validate(action.val, action.isValid),
      };
    case "TOUCH":
      return {
        ...state,
        isTouch: true,
      };
    case "RADIO_CHANGE":
      return {
        ...state,
        value: action.val,
        isValid: validate(action.val, VALIDATOR_REQUIRE()),
      };
    default:
      return state;
  }
};

const Input = (props) => {
  const [inputState, dispatch] = useReducer(inputReducer, {
    value: props.initialValue || "",
    isTouch: false,
    isValid: props.initialValid || false,
  });

  const { id, onInput } = props;
  const { value, isValid } = inputState;

  useEffect(() => {
    if (typeof onInput === "function") {
      onInput(id, value, isValid);
    }
  }, [id, value, isValid, onInput]);

  const changeHandler = (event) => {
    dispatch({
      type: "CHANGE",
      val: event.target.value,
      validators: props.validators,
    });
  };

  const touchHandler = () => {
    dispatch({
      type: "TOUCH",
    });
  };

  const radioChangeHandler = (selectedValue) => {
    dispatch({
      type: "RADIO_CHANGE",
      value: selectedValue,
    });
  };

  const renderRadioOptions = (radioOptions) => {
    return radioOptions.map((option) => (
      <label key={option.value} className={option.className}>
        <input
          type="radio"
          name={option.id}
          value={option.value}
          checked={value === option.value}
          onChange={() => radioChangeHandler(option.value)}
          onBlur={touchHandler}
        />
        <span className={props.span}>{option.label}</span>
      </label>
    ));
  };

  const element =
    props.element === "input" ? (
      <input
        id={props.id}
        type={props.type}
        placeholder={props.placeholder}
        onChange={changeHandler}
        onBlur={touchHandler}
        value={inputState.value}
      />
    ) : props.element === "radio" ? (
      <div className={props.className}>
        <label className="radio-inputs__title">{props.label}</label>
        <hr />
        {() => renderRadioOptions(props.radioOptions)}
      </div>
    ) : (
      <textarea
        id={props.id}
        rows={props.rows || 3}
        onChange={changeHandler}
        onBlur={touchHandler}
        value={inputState.value}
      />
    );

  return (
    <div
      className={`form-control ${
        !inputState.isValid && inputState.isTouch && "form-control--invalid"
      }`}
    >
      {props.element !== "radio" && (
        <label htmlFor={props.id}>{props.label}</label>
      )}
      {element}
      {!inputState.isValid && inputState.isTouch && <p>{props.errorText}</p>}
    </div>
  );
};

export default Input;
